/* --- colors --- */
/* blocks */
.header {
  font-size: 17px;
  line-height: 28px;
  position: relative;
  min-height: 825px;
  padding-bottom: 1px;
  background-color: #000000;
  background-image: url("../../img/header-bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }

.header__menu-btn {
  display: none; }

.header__navigation {
  box-sizing: border-box;
  padding-left: 15px;
  padding-right: 21px;
  max-width: 870px;
  min-height: 60px;
  margin: 0 auto;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.header__navigation-list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 32px;
  color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: baseline;
  padding-top: 28px; }

.header__navigation-item a {
  color: #ffffff;
  text-decoration: none;
  letter-spacing: -0.005em;
  opacity: 0.7; }
  .header__navigation-item a:hover {
    cursor: pointer;
    opacity: 1; }

.header__navigation-item svg {
  display: none; }

.header__inner {
  font-size: 20px;
  line-height: 32px;
  color: #ffffff;
  letter-spacing: -0.005em;
  max-width: 882px;
  margin: 255px auto 0; }

.header__inner h2 {
  font-size: 46px;
  line-height: 64px;
  letter-spacing: 1px;
  max-width: 350px;
  margin-top: 20px;
  margin-bottom: 26px; }

.header__inner p {
  max-width: 350px;
  opacity: 0.8; }

@media (min-resolution: 144dpi) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../../img/header-bg@2x.jpg"); } }

@media (max-width: 1024px) {
  .header {
    min-height: 340px;
    padding-top: 1px;
    background-image: url("../../img/header-bg-tablet.jpg"); }
  .header__navigation {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: unset;
    z-index: 1;
    background-color: #ffffff;
    box-shadow: 0 10px 6px 0 rgba(0, 0, 0, 0.11); }
    .header__navigation--opened {
      display: block; }
  .header__menu-btn {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    width: 50px;
    height: 50px;
    border: none;
    background-color: #0ad9c6; }
  .header__navigation--opened + .header__menu-btn {
    width: 50px;
    height: 50px;
    background-color: transparent; }
    .header__navigation--opened + .header__menu-btn::after, .header__navigation--opened + .header__menu-btn::before {
      content: "";
      width: 35px;
      height: 1px;
      display: block;
      background-color: #1c1c1c;
      opacity: 0.2; }
    .header__navigation--opened + .header__menu-btn::after {
      transform: rotate(45deg); }
    .header__navigation--opened + .header__menu-btn::before {
      transform: rotate(-45deg); }
    .header__navigation--opened + .header__menu-btn svg {
      display: none; }
  .header__navigation-list {
    display: block;
    min-height: 570px;
    padding-top: 100px; }
  .header__navigation-item {
    box-sizing: border-box;
    width: 100%;
    min-height: 60px;
    padding-left: 16px;
    padding-right: 16px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #f6f6f6; }
  .header__navigation-item a {
    font-size: 20px;
    line-height: 140%;
    color: #48494d; }
  .header__navigation-item svg {
    display: block; }
  .header__inner {
    display: block;
    max-width: 420px;
    margin: 0 auto;
    margin-top: 134px; }
  .header__image {
    height: auto; }
  .header__logo {
    display: block;
    position: absolute;
    top: 25px;
    left: 20px;
    z-index: 2; }
  .header__inner h2 {
    font-size: 30px;
    line-height: 42px;
    letter-spacing: 0.5px;
    max-width: unset;
    padding-left: 11px;
    margin-bottom: 20px; }
  .header__inner p {
    font-size: 18px;
    line-height: 32px;
    letter-spacing: -0.005em;
    text-align: center;
    opacity: 0.5;
    max-width: unset; } }

@media (max-width: 1024px) and (min-resolution: 144dpi) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../../img/header-bg-tablet@2x.jpg"); } }

@media (max-width: 767px) {
  .header {
    min-height: 390px;
    background-image: url("../../img/header-bg-mobile.jpg"); }
  .header__inner {
    margin-top: 120px; }
  .header__inner h2 {
    font-size: 30px;
    line-height: 42px;
    text-align: center;
    margin-bottom: 10px;
    padding-left: unset; }
  .header__inner p {
    font-size: 18px;
    line-height: 32px;
    font-weight: 300;
    text-align: center;
    padding: 0 28px; } }

@media (max-width: 767px) and (min-resolution: 144dpi) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../../img/header-bg-mobile@2x.jpg"); } }

.feedback {
  max-width: 330px;
  position: absolute;
  top: 308px;
  right: 277px; }

.feedback p {
  font-size: 17px;
  line-height: 28px;
  letter-spacing: -0.005em;
  font-weight: 600;
  text-align: center;
  color: #ffffff;
  opacity: 1; }

.feedback br {
  display: none; }

.feedback input {
  box-sizing: border-box;
  width: 100%;
  height: 48px;
  padding-left: 30px;
  margin-bottom: 20px;
  border: 1px solid #c9ccd4;
  border-radius: 5px; }
  .feedback input::placeholder {
    font-size: 15px;
    line-height: 24px;
    color: #1c1c1c;
    opacity: 0.5; }

.feedback button[type="submit"] {
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  text-align: center;
  font-weight: 600;
  box-sizing: border-box;
  padding: 0;
  width: 100%;
  height: 50px;
  border: none;
  border-radius: 5px;
  background-color: #0ad9c6; }

@media (max-width: 1024px) {
  .feedback {
    position: static;
    margin: 0 auto;
    margin-top: 100px; }
  .feedback p {
    color: #000000; }
  .feedback br {
    display: inline; } }

@media (max-width: 767px) {
  .feedback br {
    display: none; }
  .feedback {
    padding: 0 15px;
    margin-top: 69px; }
  .feedback p {
    letter-spacing: 0.5px; } }

.footer {
  padding-top: 250px;
  padding-bottom: 100px;
  text-align: center; }

.footer h2 {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  color: #1f1f1f; }

.footer address {
  font-size: 17px;
  line-height: 31px;
  font-weight: 400;
  font-style: normal;
  color: rgba(0, 0, 0, 0.7);
  max-width: 550px;
  margin: 0 auto; }

.footer__map {
  margin: 40px 0; }

.footer__mail,
.footer__tel {
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5); }

.footer__mail {
  margin-right: 90px; }

.footer__tel svg {
  fill: transparent; }

@media (max-width: 1024px) {
  .footer {
    padding-bottom: 50px; }
  .footer address {
    max-width: unset; }
  .footer__map {
    margin: 20px 0; }
  .footer__map iframe {
    width: 700px;
    height: 545px; } }

@media (max-width: 767px) {
  .footer__map {
    margin-bottom: 30px; }
  .footer__map iframe {
    width: 100%; }
  .footer__mail {
    margin-right: 0;
    margin-bottom: 20px;
    display: block; } }
